import { FC, KeyboardEvent, useEffect, useRef, useState } from 'react';
import styles from './TextEditor.module.scss';

export interface TextEditorProps {
  /**  */
  value: string;
  /**  */
  onChange: (value: string) => void;
  /**  */
  placeholder?: string;
  /**  */
  multiline?: boolean;
  /**  */
  rows?: number;
  /**  */
  disabled?: boolean;
  /**  */
  maxLength?: number;
  /**  */
  className?: string;
  /** inline modal  */
  mode?: 'inline' | 'modal';
}

/**
 * 
 * /
 */
export const TextEditor: FC<TextEditorProps> = ({
  value,
  onChange,
  placeholder = '...',
  multiline = false,
  rows = 3,
  disabled = false,
  maxLength,
  className,
  mode = 'inline',
}) => {
  const [isEditing, setIsEditing] = useState(false);
  const [tempValue, setTempValue] = useState(value);
  const inputRef = useRef<HTMLInputElement | HTMLTextAreaElement>(null);

  // 
  useEffect(() => {
    if (!isEditing) {
      setTempValue(value);
    }
  }, [value, isEditing]);

  // 
  useEffect(() => {
    if (isEditing && inputRef.current) {
      inputRef.current.focus();
      inputRef.current.select();
    }
  }, [isEditing]);

  const handleStartEdit = () => {
    if (disabled) return;
    setIsEditing(true);
    setTempValue(value);
  };

  const handleCommit = () => {
    onChange(tempValue);
    setIsEditing(false);
  };

  const handleCancel = () => {
    setTempValue(value);
    setIsEditing(false);
  };

  const handleKeyDown = (e: KeyboardEvent) => {
    if (e.key === 'Enter' && !multiline) {
      e.preventDefault();
      handleCommit();
    } else if (e.key === 'Escape') {
      handleCancel();
    }
  };

  const handleBlur = () => {
    if (!disabled) {
      handleCommit();
    }
  };

  const displayValue = value || placeholder;

  if (mode === 'inline' && !isEditing) {
    return (
      <div
        className={`${styles.textEditor} ${styles.displayMode} ${disabled ? styles.disabled : ''} ${className ?? ''}`}
        onClick={handleStartEdit}
        title={disabled ? '' : ''}
      >
        <span className={`${styles.displayValue} ${!value ? styles.placeholder : ''}`}>
          {displayValue}
        </span>
        {!disabled && <i className={`fa-solid fa-pen ${styles.editIcon}`} />}
      </div>
    );
  }

  const inputProps = {
    ref: inputRef as React.RefObject<HTMLInputElement & HTMLTextAreaElement>,
    value: tempValue,
    onChange: (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) =>
      setTempValue(e.target.value),
    onKeyDown: handleKeyDown,
    onBlur: handleBlur,
    placeholder,
    disabled,
    maxLength,
    className: styles.input,
  };

  return (
    <div className={`${styles.textEditor} ${styles.editMode} ${className ?? ''}`}>
      <div className={styles.inputWrapper}>
        {multiline ? (
          <textarea {...inputProps} rows={rows} />
        ) : (
          <input type="text" {...inputProps} />
        )}
      </div>
    </div>
  );
};
