import { FC } from 'react';
import { TextEditor } from '../TextEditor/TextEditor';

export interface TextareaEditorProps {
  /**  */
  value: string;
  /**  */
  onChange: (value: string) => void;
  /**  */
  placeholder?: string;
  /**  */
  rows?: number;
  /**  */
  disabled?: boolean;
  /**  */
  maxLength?: number;
  /**  */
  className?: string;
}

/**
 * 
 */
export const TextareaEditor: FC<TextareaEditorProps> = ({
  value,
  onChange,
  placeholder,
  rows = 4,
  disabled = false,
  maxLength,
  className,
}) => {
  return (
    <TextEditor
      value={value}
      onChange={onChange}
      placeholder={placeholder}
      multiline
      rows={rows}
      disabled={disabled}
      maxLength={maxLength}
      className={className}
    />
  );
};
