import { Schema } from '@/data_schema/schema';
import { create } from 'zustand';
import { immer } from 'zustand/middleware/immer';
import { StatData } from '../types';

interface MvuDataState {
  /** MVU  */
  data: StatData | null;
  /**  */
  loading: boolean;
  /**  */
  error: string | null;
  /**  */
  lastRefreshTime: Date | null;
}

interface MvuDataActions {
  /**  (Read) */
  refresh: () => void;
  /**  */
  updateField: (path: string, value: unknown) => Promise<boolean>;
  /**  */
  deleteField: (path: string) => Promise<boolean>;
}

type MvuDataStore = MvuDataState & MvuDataActions;

export const useMvuDataStore = create<MvuDataStore>()(
  immer((set, get) => ({
    // State
    data: null,
    loading: true,
    error: null,
    lastRefreshTime: null,

    // Actions

    /**
     * 
     */
    refresh: () => {
      set(state => {
        state.loading = true;
      });

      try {
        // 
        const variables = getVariables({
          type: 'message',
          message_id: getCurrentMessageId(),
        });

        //  stat_data
        const rawData = _.get(variables, 'stat_data', {});
        const result = Schema.safeParse(rawData);

        if (!result.success) {
          console.warn('[StatusBar] :', result.error);
          set(state => {
            state.error = `${result.error.message || ''}`;
            state.loading = false;
          });

          return;
        }

        set(state => {
          state.data = result.data;
          state.loading = false;
          state.error = null;
          state.lastRefreshTime = new Date();
        });

        console.log('[StatusBar] ');
      } catch (e) {
        console.error('[StatusBar] :', e);
        set(state => {
          state.error = e instanceof Error ? e.message : '';
          state.loading = false;
        });
      }
    },

    /**
     * 
     */
    updateField: async (path: string, value: unknown): Promise<boolean> => {
      try {
        await waitGlobalInitialized('Mvu');
        const mvuData = Mvu.getMvuData({
          type: 'message',
          message_id: getCurrentMessageId(),
        });

        // 
        _.set(mvuData, `stat_data.${path}`, value);

        // 
        await Mvu.replaceMvuData(mvuData, {
          type: 'message',
          message_id: getCurrentMessageId(),
        });

        // 
        get().refresh();

        return true;
      } catch (e) {
        console.error('[StatusBar] :', e);
        return false;
      }
    },

    /**
     * 
     */
    deleteField: async (path: string): Promise<boolean> => {
      try {
        await waitGlobalInitialized('Mvu');
        const mvuData = Mvu.getMvuData({
          type: 'message',
          message_id: getCurrentMessageId(),
        });

        // 
        _.unset(mvuData, `stat_data.${path}`);

        // 
        await Mvu.replaceMvuData(mvuData, {
          type: 'message',
          message_id: getCurrentMessageId(),
        });

        // 
        get().refresh();

        return true;
      } catch (e) {
        console.error('[StatusBar] :', e);
        return false;
      }
    },
  })),
);
