import { FC, KeyboardEvent, useState } from 'react';
import styles from './KeyValueEditor.module.scss';

export interface KeyValueEditorProps {
  /**  */
  value: Record<string, string | number>;
  /**  */
  onChange: (value: Record<string, string | number>) => void;
  /**  */
  keyPlaceholder?: string;
  /**  */
  valuePlaceholder?: string;
  /**  */
  disabled?: boolean;
  /**  */
  className?: string;
  /**  */
  valueType?: 'string' | 'number';
}

/**
 * 
 * 
 */
export const KeyValueEditor: FC<KeyValueEditorProps> = ({
  value,
  onChange,
  keyPlaceholder = '',
  valuePlaceholder = '',
  disabled = false,
  className,
  valueType = 'string',
}) => {
  const [newKey, setNewKey] = useState('');
  const [newValue, setNewValue] = useState('');
  const [editingKey, setEditingKey] = useState<string | null>(null);
  const [editingValue, setEditingValue] = useState('');

  const handleCommit = (nextValue: Record<string, string | number>) => {
    onChange(nextValue);
  };

  const handleAdd = () => {
    const trimmedKey = newKey.trim();
    if (!trimmedKey || !newValue) return;
    if (trimmedKey in value) return; // 

    const parsedValue = valueType === 'number' ? parseFloat(newValue) || 0 : newValue;
    handleCommit({ ...value, [trimmedKey]: parsedValue });
    setNewKey('');
    setNewValue('');
  };

  const handleRemove = (keyToRemove: string) => {
    if (disabled) return;
    const newObj = { ...value };
    delete newObj[keyToRemove];
    handleCommit(newObj);
  };

  const handleStartEdit = (key: string) => {
    if (disabled) return;
    setEditingKey(key);
    setEditingValue(String(value[key]));
  };

  /**  */
  const handleConfirmEdit = () => {
    if (editingKey === null) return;
    const parsedValue = valueType === 'number' ? parseFloat(editingValue) || 0 : editingValue;
    handleCommit({ ...value, [editingKey]: parsedValue });
    setEditingKey(null);
    setEditingValue('');
  };

  /** Escape  */
  const handleCancelEdit = () => {
    setEditingKey(null);
    setEditingValue('');
  };

  /**  */
  const handleBlur = () => {
    if (!disabled) {
      handleConfirmEdit();
    }
  };

  const handleKeyDown = (
    e: KeyboardEvent<HTMLInputElement | HTMLTextAreaElement>,
    action: 'add' | 'edit',
  ) => {
    // Ctrl+Enter  Cmd+Enter 
    if (e.key === 'Enter' && (e.ctrlKey || e.metaKey)) {
      e.preventDefault();
      if (action === 'add') {
        handleAdd();
      } else {
        handleConfirmEdit();
      }
    } else if (e.key === 'Escape') {
      if (action === 'edit') {
        handleCancelEdit();
      }
    }
  };

  const entries = Object.entries(value);

  return (
    <div
      className={`${styles.keyValueEditor} ${disabled ? styles.disabled : ''} ${className ?? ''}`}
    >
      <div className={styles.content}>
        {/*  */}
        {entries.length > 0 && (
          <div className={styles.entries}>
            {entries.map(([key, val]) => (
              <div key={key} className={styles.entry}>
                <span className={styles.entryKey}>{key}</span>
                {editingKey === key ? (
                  <div className={styles.editWrapper}>
                    {valueType === 'number' ? (
                      <input
                        type="number"
                        value={editingValue}
                        onChange={e => setEditingValue(e.target.value)}
                        onKeyDown={e => handleKeyDown(e, 'edit')}
                        onBlur={handleBlur}
                        className={styles.editInput}
                        autoFocus
                      />
                    ) : (
                      <textarea
                        value={editingValue}
                        onChange={e => setEditingValue(e.target.value)}
                        onKeyDown={e => handleKeyDown(e, 'edit')}
                        onBlur={handleBlur}
                        className={styles.editTextarea}
                        rows={3}
                        autoFocus
                        placeholder="  Esc "
                      />
                    )}
                  </div>
                ) : (
                  <>
                    <span
                      className={styles.entryValue}
                      onClick={() => handleStartEdit(key)}
                      title=""
                    >
                      {val}
                    </span>
                    {!disabled && (
                      <button
                        className={styles.removeBtn}
                        onClick={() => handleRemove(key)}
                        title=""
                      >
                        <i className="fa-solid fa-trash" />
                      </button>
                    )}
                  </>
                )}
              </div>
            ))}
          </div>
        )}

        {/*  */}
        {!disabled && (
          <div className={styles.addRow}>
            <input
              type="text"
              value={newKey}
              onChange={e => setNewKey(e.target.value)}
              onKeyDown={e => handleKeyDown(e, 'add')}
              placeholder={keyPlaceholder}
              className={styles.addInput}
            />
            {valueType === 'number' ? (
              <input
                type="number"
                value={newValue}
                onChange={e => setNewValue(e.target.value)}
                onKeyDown={e => handleKeyDown(e, 'add')}
                placeholder={valuePlaceholder}
                className={styles.addInput}
              />
            ) : (
              <textarea
                value={newValue}
                onChange={e => setNewValue(e.target.value)}
                onKeyDown={e => handleKeyDown(e, 'add')}
                placeholder={`${valuePlaceholder} (Ctrl+Enter )`}
                className={styles.addTextarea}
                rows={2}
              />
            )}
            <button
              className={styles.addBtn}
              onClick={handleAdd}
              disabled={!newKey.trim() || !newValue}
              title=" (Ctrl+Enter)"
            >
              <i className="fa-solid fa-plus" />
            </button>
          </div>
        )}

        {entries.length === 0 && disabled && <span className={styles.empty}></span>}
      </div>
    </div>
  );
};
