import { FC, ReactNode, useCallback, useEffect } from 'react';
import styles from './ConfirmModal.module.scss';

/**  */
export interface ConfirmModalRow {
  /**  */
  label: string;
  /**  */
  value: ReactNode;
}

/**  */
export type ConfirmButtonVariant = 'primary' | 'danger' | 'secondary';

/**  */
export interface ConfirmModalButton {
  /**  */
  text: string;
  /**  */
  variant?: ConfirmButtonVariant;
  /**  */
  onClick: () => void;
  /**  */
  disabled?: boolean;
}

export interface ConfirmModalProps {
  /**  */
  open: boolean;
  /**  */
  title: string;
  /** - */
  rows?: ConfirmModalRow[];
  /**  rows */
  children?: ReactNode;
  /**  */
  buttons?: ConfirmModalButton[];
  /**  */
  onClose?: () => void;
  /**  true */
  closeOnOverlay?: boolean;
  /**  */
  className?: string;
}

/**
 *  iframe  iframe 
 *  iframe  iframe 
 */
const scrollToIframe = () => {
  const frameElement = window.frameElement;
  if (frameElement) {
    frameElement.scrollIntoView({ behavior: 'smooth', block: 'center' });
  }
};

/**
 * 
 * 
 */
export const ConfirmModal: FC<ConfirmModalProps> = ({
  open,
  title,
  rows,
  children,
  buttons,
  onClose,
  closeOnOverlay = true,
  className,
}) => {
  //  iframe 
  useEffect(() => {
    if (open) {
      scrollToIframe();
    }
  }, [open]);

  /**  */
  const handleOverlayClick = useCallback(() => {
    if (closeOnOverlay && onClose) {
      onClose();
    }
  }, [closeOnOverlay, onClose]);

  /**  */
  const handleModalClick = useCallback((event: React.MouseEvent) => {
    event.stopPropagation();
  }, []);

  /**  */
  const getButtonClass = (variant: ConfirmButtonVariant = 'primary') => {
    switch (variant) {
      case 'danger':
        return styles.btnDanger;
      case 'secondary':
        return styles.btnSecondary;
      case 'primary':
      default:
        return styles.btnPrimary;
    }
  };

  if (!open) return null;

  return (
    <div className={styles.overlay} onClick={handleOverlayClick}>
      <div className={`${styles.modal} ${className ?? ''}`} onClick={handleModalClick}>
        <div className={styles.title}>{title}</div>

        {/*  */}
        {children && <div className={styles.content}>{children}</div>}

        {/*  */}
        {!children && rows && rows.length > 0 && (
          <div className={styles.body}>
            {rows.map((row, index) => (
              <div key={index} className={styles.row}>
                <span className={styles.label}>{row.label}</span>
                <span className={styles.value}>{row.value}</span>
              </div>
            ))}
          </div>
        )}

        {/*  */}
        {buttons && buttons.length > 0 && (
          <div className={styles.actions}>
            {buttons.map((btn, index) => (
              <button
                key={index}
                className={getButtonClass(btn.variant)}
                onClick={btn.onClick}
                disabled={btn.disabled}
              >
                {btn.text}
              </button>
            ))}
          </div>
        )}
      </div>
    </div>
  );
};
