import { useCallback, useState } from 'react';
import { useMvuDataStore } from '../stores';

/**
 * 
 */
export interface DeleteTarget {
  /**  */
  type: string;
  /**  */
  path: string;
  /**  */
  name: string;
}

/**
 *  Hook 
 */
export interface UseDeleteConfirmReturn {
  /**  */
  deleteTarget: DeleteTarget | null;
  /**  */
  setDeleteTarget: (target: DeleteTarget | null) => void;
  /**  */
  handleDelete: () => Promise<void>;
  /**  */
  cancelDelete: () => void;
  /**  */
  isConfirmOpen: boolean;
}

/**
 *  Hook
 *  StatusTab, DestinyTab, ItemsTab 
 *
 * @example
 * ```tsx
 * const { deleteTarget, setDeleteTarget, handleDelete, cancelDelete, isConfirmOpen } = useDeleteConfirm();
 *
 * // 
 * setDeleteTarget({ type: '', path: '..', name: '' });
 *
 * // 
 * <ConfirmModal
 *   open={isConfirmOpen}
 *   title={`${deleteTarget?.type ?? ''}`}
 *   rows={[
 *     { label: '', value: deleteTarget?.name ?? '' },
 *     { label: '', value: '' },
 *   ]}
 *   buttons={[
 *     { text: '', variant: 'danger', onClick: handleDelete },
 *     { text: '', variant: 'secondary', onClick: cancelDelete },
 *   ]}
 *   onClose={cancelDelete}
 * />
 * ```
 */
export const useDeleteConfirm = (): UseDeleteConfirmReturn => {
  const [deleteTarget, setDeleteTarget] = useState<DeleteTarget | null>(null);
  const { deleteField } = useMvuDataStore();

  const handleDelete = useCallback(async () => {
    if (!deleteTarget) return;

    try {
      await deleteField(deleteTarget.path);
      toastr.success(`${deleteTarget.name}`);
    } catch {
      toastr.error('');
    } finally {
      setDeleteTarget(null);
    }
  }, [deleteTarget, deleteField]);

  const cancelDelete = useCallback(() => {
    setDeleteTarget(null);
  }, []);

  return {
    deleteTarget,
    setDeleteTarget,
    handleDelete,
    cancelDelete,
    isConfirmOpen: !!deleteTarget,
  };
};
