import { FC } from 'react';
import type { DeleteTarget } from '../../../core/hooks/use-delete-confirm';
import { ConfirmModal } from '../ConfirmModal';

export interface DeleteConfirmModalProps {
  /**  */
  open: boolean;
  /**  */
  target: DeleteTarget | null;
  /**  */
  onConfirm: () => void;
  /**  */
  onCancel: () => void;
}

/**
 * 
 * 
 */
export const DeleteConfirmModal: FC<DeleteConfirmModalProps> = ({
  open,
  target,
  onConfirm,
  onCancel,
}) => {
  return (
    <ConfirmModal
      open={open}
      title={`${target?.type ?? ''}`}
      rows={[
        { label: '', value: target?.name ?? '' },
        { label: '', value: '' },
      ]}
      buttons={[
        { text: '', variant: 'danger', onClick: onConfirm },
        { text: '', variant: 'secondary', onClick: onCancel },
      ]}
      onClose={onCancel}
    />
  );
};
